export enum ErrorCorrectionLevel {
  LOW = 'L',
  MEDIUM = 'M',
  QUARTILE = 'Q',
  HIGH = 'H'
}

export enum QRCodeFormat {
  PNG = 'png',
  SVG = 'svg'
}

export enum QRCodeShape {
  SQUARE = 'square',
  ROUNDED = 'rounded',
  CIRCLE = 'circle',
  DOT = 'dot',
  STAR = 'star',
  HEXAGON = 'hexagon',
  DIAMOND = 'diamond',
  TRIANGLE = 'triangle'
}

export interface QRCodeOptions {
  size?: number;
  errorCorrectionLevel?: ErrorCorrectionLevel;
  color?: {
    dark?: string;
    light?: string;
  };
  margin?: number;
  scale?: number;
  logo?: {
    src?: string;
    size?: number;
  };
  shape?: QRCodeShape;
  cornerRadius?: number;
  complexity?: number; //  (1-10)
  moduleSize?: number; //  (50-150)
  moduleSpacing?: number; //  (0-10)
  randomness?: number; //  (0-100)
  animation?: {
    enabled?: boolean;
    type?: 'fade' | 'scale' | 'rotate' | 'bounce' | 'pulse';
    duration?: number; // (ms)
    delay?: number; // (ms)
    stagger?: number; // (ms)
    direction?: 'forward' | 'reverse' | 'alternate';
  };
}

export interface QRCodeResult {
  dataURL: string;
  options: QRCodeOptions;
}

export interface ExportOptions {
  fileName?: string;
}
